import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet-async';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { useNavigate } from 'react-router-dom';

//firestore
import { useFirebaseAuth } from 'src/contexts/FirebaseAuthProvider';
import { collection, getDocs, setDoc, doc, deleteDoc } from "firebase/firestore"; 
import { UserInterface, convertUserQuerySnapshots } from 'src/models/users';
import { RiderInterface } from 'src/models/rider';
import axios from 'axios';

//componets
import UserTable from './UserTable';
import SnackbarApp, { SnackbarProps } from 'src/components/Snackbar';

//icons
import AddIcon from '@mui/icons-material/Add';


function UserList() {
    const { database } = useFirebaseAuth();
    const navigate = useNavigate();
    const [pushUpdate, setPushUpdate] = useState<boolean>(false);
    const [butilUsers, setButilUsers] = useState<UserInterface[]>([]);
    const [snackbarApp, setSnackBarApp] = useState<SnackbarProps>({
        openSnackbar: false,
        success: true,
        message: ''
    });

    useEffect(() => {
        async function getUsers() {
            let userList: UserInterface[] = [];
            const querySnapshot = await getDocs(collection(database, "accounts"));

            querySnapshot.forEach((doc) => {
                const currentUser = convertUserQuerySnapshots(doc);
                userList.push(currentUser.getUser());
            });

            setButilUsers(state => userList);
        }

        getUsers();
    }, [database, pushUpdate]);

    function promoteUserToRider(userId: string) {
        const newRider: RiderInterface = {
            active: true,
            assignedDeliver: []
        }

        setDoc(doc(database, "delivery", userId), newRider).then(response => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: true, message: 'Added rider successfully!' });
        }).catch(error => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: false, message: 'Failed to add rider.' });
        });
    }

    function promoteUserToAdmin(userId: string) {
        const newAdmin = {
            active: true
        }

        setDoc(doc(database, "admin", userId), newAdmin).then(response => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: true, message: 'Added admin successfully!' });
        }).catch(error => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: false, message: 'Failed to add admin.' });
        });
    }

    function deleteUser(userId: string) {
        axios.get('https://butiladmin.herokuapp.com/api/jsdhfyyrhv7&ggeimcv_$hrjeuifcv/user/delete/' + userId).then(response => {
            deleteDoc(doc(database, "accounts", userId)).then(() => {
                setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: true, message: 'Deleted user successfully!' });
            }).catch(error => {
                setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: false, message: 'Failed to delete user.' });
            })
        }).catch(error => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: false, message: 'Failed to delete user.' });
        });
        setPushUpdate(!pushUpdate);
    }

    return (
        <>
            <Helmet>
                <title>Users</title>
            </Helmet>
            <Container>
                <Box sx={{ 
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        marginTop: 5,
                        marginBottom: 3
                    }}>
                    <Typography variant="h1" color="primary">Users</Typography>
                    <Button variant="contained" startIcon={<AddIcon/>} onClick={event => navigate('/dashboards/users/create')}>Add User</Button>
                </Box>
                <UserTable users={butilUsers} promoteToRider={userId => promoteUserToRider(userId)} promoteToAdmin={userId => promoteUserToAdmin(userId)} deleteUser={userId => deleteUser(userId)} />
            </Container>
            <SnackbarApp openSnackbar={snackbarApp.openSnackbar} success={snackbarApp.success} message={snackbarApp.message} setOpenSnackbar={value => setSnackBarApp({ ...snackbarApp, openSnackbar: value })} />
        </>
    )
}

export default UserList
