import { useState, useRef } from 'react';

import {
  Box,
  Menu,
  IconButton,
  ListItemText,
  ListItem,
  List,
  Typography
} from '@mui/material';

import MoreVertTwoToneIcon from '@mui/icons-material/MoreVertTwoTone';


interface Props {
    size: number,
    batchPromoteToRider: () => void,
    batchPromoteToAdmin: () => void
}

function BulkedActions({ size, batchPromoteToRider, batchPromoteToAdmin }: Props) {
  const [onMenuOpen, menuOpen] = useState<boolean>(false);
  const moreRef = useRef<HTMLButtonElement | null>(null);

  const openMenu = (): void => {
    menuOpen(true);
  };

  const closeMenu = (): void => {
    menuOpen(false);
  };

  function promoteUsersToAdmin() {
    batchPromoteToAdmin();
    closeMenu();
  }

  function promoteUsersToRider() {
    batchPromoteToRider();
    closeMenu();
  }

  return (
    <>
      <Box display="flex" alignItems="center" justifyContent="space-between">
        <Box display="flex" alignItems="center">
          <Typography variant="h5" color="text.secondary">
            {size > 1 ? size + ' users selected.' : size + ' user selected.'}
          </Typography>
        </Box>
        <IconButton
          color="primary"
          onClick={openMenu}
          ref={moreRef}
          sx={{ ml: 2, p: 1 }}
        >
          <MoreVertTwoToneIcon />
        </IconButton>
      </Box>

      <Menu
        keepMounted
        anchorEl={moreRef.current}
        open={onMenuOpen}
        onClose={closeMenu}
        anchorOrigin={{
          vertical: 'center',
          horizontal: 'center'
        }}
        transformOrigin={{
          vertical: 'center',
          horizontal: 'center'
        }}

      >
        <List sx={{ p: 1 }} component="nav">
          <ListItem button onClick={event =>  promoteUsersToRider()}>
            <ListItemText primary="Promote user as Rider"/>
          </ListItem>
          <ListItem button onClick={event =>  promoteUsersToAdmin()}>
            <ListItemText primary="Promote user as Admin" />
          </ListItem>
        </List>
      </Menu>
    </>
  );
}

export default BulkedActions;
