import React, { useState, useEffect } from 'react';
import {
    Tooltip,
    Divider,
    Box,
    FormControl,
    InputLabel,
    Card,
    Checkbox,
    IconButton,
    Table,
    TableBody,
    TableCell,
    TableHead,
    TablePagination,
    TableRow,
    TableContainer,
    Select,
    MenuItem,
    Typography,
    useTheme,
    CardHeader
  } from '@mui/material';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import DeliveryDiningIcon from '@mui/icons-material/DeliveryDining';
import BulkedActions from './BulkedActions';
import { UserInterface } from 'src/models/users';

type UserGender = 'male' | 'female';

const statusOptions = [
    {
        id: 'all',
        name: 'All'
    },
    {
        id: 'male',
        name: 'Male'
    },
    {
        id: 'female',
        name: 'Female'
    }
];

interface Filters {
    status?: UserGender;
}

interface TableProps {
    users: UserInterface[],
    promoteToRider: (userId: string) => void,
    promoteToAdmin: (userId: string) => void,
    deleteUser: (userId: string) => void
}

const applyPagination = (
    userList: UserInterface[],
    page: number,
    limit: number
  ): UserInterface[] => {
    return userList.slice(page * limit, page * limit + limit);
}

export default function UserTable({ users, promoteToRider, promoteToAdmin, deleteUser }: TableProps) {
    const [selectedUsers, setSelectedUsers] = useState<string[]>([]);
    const [page, setPage] = useState<number>(0);
    const [limit, setLimit] = useState<number>(5);
    const [filters, setFilters] = useState<Filters>({
        status: null
    });
    const [filteredUsers, setFilteredUsers] = useState<UserInterface[]>([]);
    const [paginatedUsers, setPaginatedUsers] = useState<UserInterface[]>([]);
    const theme = useTheme();

    useEffect(() => {
        switch (filters.status) {
            case 'male':
                const newMaleArray = users.filter(user => user.gender === 'Male');
                setFilteredUsers(state => newMaleArray);
                break;
            case 'female':
                const newFemaleArray = users.filter(user => user.gender === 'Female');
                setFilteredUsers(state => newFemaleArray);
                break;
            default: 
                setFilteredUsers(state => users);
        }
    }, [users, filters])

    useEffect(() => {
        const pageUsers = applyPagination(filteredUsers, page, limit);
        setPaginatedUsers(state => pageUsers)
    }, [page, limit, filteredUsers])

    const handleStatusChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
        let value = null;
    
        if (e.target.value !== 'all') {
          value = e.target.value;
        }
    
        setFilters((prevFilters) => ({
          ...prevFilters,
          status: value
        }));
    };

    const handlePageChange = (event: any, newPage: number): void => {
        setPage(newPage);
    }
    
    const handleLimitChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
        setLimit(parseInt(event.target.value));
    }

    function handleSelectOneUser(
        event: React.ChangeEvent<HTMLInputElement>,
        userId: string
      ): void {
        if (!selectedUsers.includes(userId)) {
          setSelectedUsers((prevSelected) => [
            ...prevSelected,
            userId
          ]);
        } else {
          setSelectedUsers((prevSelected) =>
            prevSelected.filter((id) => id !== userId)
          );
        }
    }

    function batchPromoteToAdmin() {
        selectedUsers.forEach(id => {
            promoteToAdmin(id);
        });
        setSelectedUsers([]);
    }

    function batchPromoteToRider() {
        selectedUsers.forEach(id => {
            promoteToRider(id);
        });
        setSelectedUsers([]);
    }

    return (
        <Card>
            {selectedUsers.length > 0 && (
                <Box flex={1} p={2}>
                    <BulkedActions size={selectedUsers.length} batchPromoteToAdmin={() => batchPromoteToAdmin()} batchPromoteToRider={() => batchPromoteToRider()} />
                </Box>
            )}
            {selectedUsers.length === 0 && (
                <CardHeader
                    action={
                        <Box width={150}>
                        <FormControl fullWidth variant="outlined">
                            <InputLabel>Status</InputLabel>
                            <Select
                                value={filters.status || 'all'}
                                onChange={handleStatusChange}
                                label="Status"
                                autoWidth
                            >
                                {statusOptions.map((statusOption) => (
                                    <MenuItem key={statusOption.id} value={statusOption.id}>
                                    {statusOption.name}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                        </Box>
                    }
                    title="User List"
                />
            )}
            <Divider />
            <TableContainer>
                <Table>
                    <TableHead>
                    <TableRow>
                        <TableCell padding="checkbox">
                        </TableCell>
                        <TableCell>User ID</TableCell>
                        <TableCell>First Name</TableCell>
                        <TableCell>Last Name</TableCell>
                        <TableCell>Gender</TableCell>
                        <TableCell>Contact</TableCell>
                        <TableCell align="right">Actions</TableCell>
                    </TableRow>
                    </TableHead>
                    <TableBody>
                        {paginatedUsers.map(user => {
                            const isUserSelected = selectedUsers.includes(user.userId);
                            return (
                                <TableRow
                                    hover
                                    key={user.userId}
                                    selected={isUserSelected}
                                >
                                    <TableCell padding="checkbox">
                                        <Checkbox
                                            color="primary"
                                            checked={isUserSelected}
                                            value={isUserSelected}
                                            onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
                                                handleSelectOneUser(event, user.userId)
                                            }
                                        />
                                    </TableCell>
                                    <TableCell>
                                        <Typography
                                            variant="body1"
                                            fontWeight="bold"
                                            color="text.primary"
                                            gutterBottom
                                            noWrap
                                        >
                                            {user.userId}
                                        </Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Typography
                                            variant="body1"
                                            fontWeight="bold"
                                            color="text.primary"
                                            gutterBottom
                                            noWrap
                                        >
                                            {user.firstName}
                                        </Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Typography
                                            variant="body1"
                                            fontWeight="bold"
                                            color="text.primary"
                                            gutterBottom
                                            noWrap
                                        >
                                            {user.lastName}
                                        </Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Typography
                                            variant="body1"
                                            fontWeight="bold"
                                            color="text.primary"
                                            gutterBottom
                                            noWrap
                                        >
                                            {user.gender}
                                        </Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Typography
                                            variant="body1"
                                            fontWeight="bold"
                                            color="text.primary"
                                            gutterBottom
                                            noWrap
                                        >
                                            {user.contact}
                                        </Typography>
                                    </TableCell>
                                    <TableCell align="right">
                                        <Tooltip title="Promote to Rider" arrow>
                                        <IconButton
                                            sx={{
                                            '&:hover': { background: theme.colors.info.lighter },
                                            color: theme.palette.secondary.main
                                            }}
                                            color="inherit"
                                            size="small"
                                            onClick={event => promoteToRider(user.userId)}
                                        >
                                            <DeliveryDiningIcon fontSize="small" />
                                        </IconButton>
                                        </Tooltip>
                                        <Tooltip title="Promote to Admin" arrow>
                                        <IconButton
                                            sx={{
                                            '&:hover': { background: theme.colors.warning.lighter },
                                            color: theme.palette.secondary.main
                                            }}
                                            color="inherit"
                                            size="small"
                                            onClick={event => promoteToAdmin(user.userId)}
                                        >
                                            <AdminPanelSettingsIcon fontSize="small" />
                                        </IconButton>
                                        </Tooltip>
                                        <Tooltip title="Delete User" arrow>
                                        <IconButton
                                            sx={{
                                            '&:hover': {
                                                background: theme.colors.error.lighter
                                            },
                                            color: theme.palette.error.main
                                            }}
                                            color="inherit"
                                            size="small"
                                            onClick={event =>  deleteUser(user.userId)}
                                        >
                                            <DeleteOutlineIcon fontSize="small" />
                                        </IconButton>
                                        </Tooltip>
                                    </TableCell>
                                </TableRow>
                            )
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
            <Box p={2}>
            <TablePagination
                component="div"
                count={filteredUsers.length}
                onPageChange={handlePageChange}
                onRowsPerPageChange={handleLimitChange}
                page={page}
                rowsPerPage={limit}
                rowsPerPageOptions={[5, 10, 25, 30]}
            />
            </Box>
        </Card>
    )
}